import React, { useState } from 'react';
import "../css/evalForm.css";
import { Link } from 'gatsby';

function form() {
    const [values, setValues] = useState({
        knowledge: '', creative: '', interpersonal: '', citizenship: '', mastery: '', 
            contributions: '', teamwork: '', percent: '', improve: ''
    });

    const set = name => {
        return ({ target: { value } }) => {
            setValues(oldValues => ({...oldValues, [name]: value }));
        }
    }

    return (
        <div class="form">
            <form>
                <label class="titlelabel"><h3>Peer Evaluation For: </h3></label>
                <select name="names" id="name">
                    <option value="default" selected="true">Choose one name.</option>
                    <option value="jacob">Jacob Mims</option>
                    <option value="pavan">Pavan Sastri</option>
                    <option value="kirk">Kirk Graham</option>
                    <option value="jacquelyn">Jacquelyn</option>
                    <option value="allie">Allie Ahwee-Marrah</option>
                    <option value="derek">Derek Stoughton</option>
                    <option value="natalie">Natalie Calizon</option>
                    <option value="vignesh">Vignesh Sinha</option>
                </select>
                <div></div><br/>

                <bold>1. What grade out of 10 do you think this individual should receive based on their efforts with the group project?</bold><br/>
                <label class="formlabels">Disciplinary + Multidisciplinary Knowledge  </label>
                <input type="text" id="knowledge" name="knowledge" size="3"></input><br/>
                <label class="formlabels">Intellectual + Creative Skills  </label>
                <input type="text" id="creative" name="creative" size="3"></input><br/>
                <label class="formlabels">Interpersonal Skills  </label>
                <input type="text" id="interpersonal" name="interpersonal" size="3"></input><br/>
                <label class="formlabels">Global Citizenship  </label>
                <input type="text" id="citizenship" name="citizenship" size="3"></input><br/>
                <label class="formlabels">Personal Mastery  </label>
                <input type="text" id="mastery" name="mastery" size="3"></input>
                <div></div><br/>

                <bold>2. Please describe the contributions of this team member below.</bold><br/>
                <textarea id="contributions" name="contributions" rows="10" cols="50"></textarea>
                <div></div><br/>

                <bold>3. Please select the option that best aligns to your analysis of the member's teamwork and communication skills.</bold>
                <div class="radio">
                    <input type="radio" id="choice1" name="teamwork" value="terrible"></input>
                    <label for="choice1">Terrible</label>
                    <input type="radio" id="choice2" name="teamwork" value="subpar"></input>
                    <label for="choice2">Sub-Par</label>
                    <input type="radio" id="choice3" name="teamwork" value="average"></input>
                    <label for="choice3">Average</label>
                    <input type="radio" id="choice4" name="teamwork" value="good"></input>
                    <label for="choice4">Good</label>
                    <input type="radio" id="choice5" name="teamwork" value="excellent"></input>
                    <label for="choice5">Excellent</label>
                </div>
                <div></div><br/>

                <bold>4. What % contribution did this member have towards the project?</bold><br/>
                <input type="text" id="percent" name="percent" size="3"></input>%
                <div></div><br/>

                <bold>5. What could this member do to improve?</bold><br/>
                <textarea id="improve" name="improve" rows="5" cols="50"></textarea>

                <div class="buttons">
                    <button type="submit"><Link to="/success">Submit</Link></button>
                    <button type="cancel"><Link to="/menu">Cancel</Link></button>
                </div>
            </form>
        </div>
    );
}

export default form;